.game-card{
    max-width: 200px;
    margin: 20px auto;
    padding: 10px 10px;
}

.game-cover{
    width: 100%;
    min-height: 220px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 4px;
    box-shadow: 0px 0px 5px black;
}
.game-name{
    margin-top: 8px;
    font-weight: 100;
}